.AdoptMain__header {
    text-transform: capitalize;
  }
  
  .AdoptMain__img {
    max-height: 60vh;
    max-width: 60%;
  }
  
  .photoButtonsContainer {
    display: flex;
    align-content: center;
    justify-content: center;
    flex-wrap: nowrap;
    max-width: 100vw;
  }
  
  .AdoptMain__button {
    background-color: white;
    color: black;
    margin: 0 1rem;
    font-size: 3rem;
    border: none;
  }
  
  @media screen and (min-width: 425px) {
    .AdoptMain__div.body {
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
    }
  
